/* ========================================== */

.alert-enter {
    margin-right: -80%;
}

.alert-enter-active {
    margin-right: 0;
    transition: margin-right 300ms ease-out;
}

.alert-exit {
    margin-right: 0;
}

.alert-exit-active {
    margin-right: -80%;
    transition: margin-right 300ms ease-out;
    color: rgb(43, 43, 83)
}





.phone-enter {
    /* margin-top: 30px; */
    opacity: 0;
    transform: scaleY(0)
}

.phone-enter-active {
    /* margin-top: 0px;*/
    transform: scaleY(1);

    opacity: 1;
    transition: transform 300ms ease-out, opacity 300ms ease-out
}


.phone-exit {
    opacity: 1;
}

.phone-exit-active {
    opacity: 0;
    transition: opacity 300ms ease-out;
}